import React, { Component } from "react";

class GetTransferDetails extends Component {
    constructor() {
        super();
        this.state = {
          selectedOption: ''
        };
      this.radioChange = this.radioChange.bind(this);
    }
    
      radioChange(e) {
        this.setState({
          selectedOption: e.currentTarget.value
        });
      }
      
      render() {
        return (
          <div>
            
            <input type="radio"
                   value="testnet2"
                   checked={this.state.selectedOption === "testnet2"}
                   onChange={this.radioChange} />Testnet2
    
            <input type="radio"
                   value="testnet3"
                   checked={this.state.selectedOption === "testnet3"}
                   onChange={this.radioChange}/>Testnet3
            
            <h3>this.state.selectedOption: {this.state.selectedOption}</h3>
          </div> 
        );
      }
}

export default GetTransferDetails;



